import React, {useEffect , useState} from 'react';

import {Panel, PanelHeader} from '@vkontakte/vkui/';

import fire_fon_img from '../img/fire_fon_img.jpg'
import tropical_rain_fon_img from '../img/tropical_rain_fon_img.jpg'

import './Song.css'

const Song = ({ id, setPlaer, image  }) => {
	const [fon, setFon] = useState(fire_fon_img)

	useEffect(() => {
			switch (image) {
				case 1 :
					setFon(fire_fon_img)
					break
				case 2 :
					setFon(tropical_rain_fon_img)
					break
			}
	}, [])

	return (
		<Panel id={id}>
		<PanelHeader>Плеер</PanelHeader>
		<div className='song-menu' style={{backgroundImage: `url(${fon})`}}>
			<div className='equalizer-div'>
			<button className="equalizer">
   				<span className="eq1"></span>
   				<span className="eq2"></span>
   				<span className="eq3"></span>
				<span className="eq4"></span>
				<span className="eq5"></span>
			</button>
			</div>
		</div>
		
		<div className='active-button' onClick={() => {
			setPlaer()
		}}></div>
		</Panel>
	)
} 


export default Song;