import React, {useEffect} from 'react';

import {Panel, PanelHeader, Header} from '@vkontakte/vkui/';
import './Home.css'

import fire from '../audio/fire.mp3'
import tropical_rain from '../audio/tropical_rain.mp3'

import fire_img from '../img/fire_img.png'
import tropical_rain_img from '../img/tropical_rain_img.png'


const Home = ({ id, setAudio, setPlaer, goView, setImage }) => {

	return (
	<Panel id={id}>
		<PanelHeader>Подборка</PanelHeader>
		<div className='audio-fles'>
			<img className='audio-fle' src={fire_img} alt='fire' onClick={() => {
				setAudio(fire)
				setImage(1)
				goView({currentTarget: {dataset: {story: 'song'}}})
			}}/>
			<img className='audio-fle' src={tropical_rain_img} alt='fire' onClick={() => {
				setAudio(tropical_rain)
				setImage(2)
				goView({currentTarget: {dataset: {story: 'song'}}})
			}}/>
		</div>
	</Panel>)
}
        
export default Home;
